import logo from './logo.svg';
import './App.css';
import React from 'react';
import {
  atan2, chain, derivative, e, evaluate, log, pi, pow, round, sqrt
} from 'mathjs'


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      display: "0",
      decimalUsed: false,
      numberClears: false
    };

  this.handleKeyPress = this.handleKeyPress.bind(this);
  this.smoothe = this.smoothe.bind(this);
  this.calculate = this.calculate.bind(this);



  }

  smoothe = (s) => {
    const re = /^0+/i;
    const newS = s.replace(re, '');
    const re2 = /([\+, \-, \*, \/])0+/i;
    const re3 = /([\+, \-, \*, \/, \.])([\/, \*])/i;
    // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/replace
    const re4 = /\.\d+(\.)/i;
    return newS.replace(re2, '').replace(re3, "$2");

  }

  calculate = (exp) => {
    try {
      const result = evaluate(exp);
      return result;
    } catch(err) {
      return 'invalid equation'
    }
  }

  handleKeyPress = (event, wrap = this.smoothe, calc = this.calculate) => {
    switch(event.target.value) {
      case "reset":
        this.setState(
          {
            display: "0",
          }
        )
        break;
      case "=":
        this.setState(
          {
            display: calc(this.state.display + "+0"),
            decimalUsed: true,
            numberClears: true   
          }
        )
        break;
      case ".":
        this.setState({display: wrap(this.state.display + event.target.value),
          decimalUsed: true});
        break;
      case "+":
      case "-":
      case "/":
      case "*":
        this.setState(
          {
          display: wrap(this.state.display + event.target.value),
          decimalUsed: false,
          numberClears: false
          }
        )
        break;
      default:
        this.setState(
          {
          display: this.state.numberClears ? wrap(event.target.value) : wrap(this.state.display + event.target.value),
          numberClears: false
          }
        )
    }

    
  };

  render () {
    return (
      <div className="App container d-flex align-items-center justify-content-center vh-100">
        <Structure display={this.state.display} handleKeyPress={this.handleKeyPress} decimalUsed={this.state.decimalUsed}/>
      </div>
    );
  };
 
}

const ButtonDiv = (val, func, idName, decimal) => {
  return (
    <div className="ms-auto col-3 py-2" key={val}>
      <button className="btn btn-dark text-warning w-100 text-center" value={val} onClick={func} id={idName} disabled={decimal}>{val}</button>
    </div>
  )
}

class Structure extends React.Component {
  constructor(props) {
    super(props);
  }


  // calc design inspiration:
  // https://dev.to/codeply/simple-calculator-using-vue-bootstrap-5-27bn

  render () {
    const items = ["1", "2", "3", "=" , "4", "5", "6", "-", "7", "8", "9", "+", "0", ".", "*", "/"];
    const item_names = ["one", "two", "three", "equals" , "four", "five", "six", "subtract", "seven", "eight", "nine", "add", "zero", "decimal", "multiply", "divide"];
    const items_mapped = items.map((x, y) => ButtonDiv(x, this.props.handleKeyPress, item_names[y], x == "." ? this.props.decimalUsed: false));

    return(
      <div className="row">
        <div className="col-xxl-3 col-lg-4 col-md-4 col-sm-6 mx-auto bg-secondary rounded-3 shadow-sm p-3 border-5 border-dark border-end border-bottom">
            <div className="form-control form-control-lg text-primary fw-bold overflow-auto fs-3" id="display">
              {this.props.display}
            </div>
            <div className="row g-0 text-center mt-2">
                <div className="col-auto text-white">
                    <div className="row g-1 g-lg-1">
                      {items_mapped}
                    </div>
                    <div className="row g-1 g-lg-1">
                      <div className="ms-auto col-12 py-2" >
                        <button id="clear" className="btn btn-dark text-warning w-100 text-center" value="reset" onClick={this.props.handleKeyPress}>reset</button>
                      </div>
                    </div>
                    
                </div>
            </div>
        </div>
        <div className="row g-1 g-lg-1">
          <div className="ms-auto col-12 py-2" >
            <p>Created by <a href="https://github.com/michaelkozin" target="_blank" rel="noreferrer">Michael Kozin</a></p>
          </div>
        </div>
      </div>

    )
  }
}

export default App;
